import React, { useEffect, useState } from 'react'
import { Button, Dropdown, Form, Table } from 'semantic-ui-react'

export const Choices = () => {
    const [row, setrow] = useState([])

    const table = () => {
        var a = parseInt(document.getElementById("num").value)
        var data = []
        for (var x = 0; x < a; x++) {
            data.push(x)
        }

        setrow(data)
    }
    useEffect(() => {
        console.log(row)
    }, [row])
    return (
        <div>
            <Form>
                <Form.Input id="num" />
                <Button onClick={table}>Click</Button>
            </Form>
            {row.length > 0 ?
                <Table>
                    <Table.Header>
                        <Table.HeaderCell>B</Table.HeaderCell>
                    </Table.Header>
                    <Table.Body>
                        {row.map(r => (
                            <Table.Row key={r}>
                                <Table.Cell>
                                    <Dropdown text="Opening and Closing Ranks" pointing name="ranks">
                                        <Dropdown.Menu>
                                            <Dropdown.Item text="IITs" name="IIT" path="ranks" />
                                            <Dropdown.Item text="IIITs" name="IIIT" path="ranks" />
                                            <Dropdown.Item text="NITs" name="NIT" path="ranks" />
                                            <Dropdown.Item text="GFTIs" name="GFTI" path="ranks" />
                                        </Dropdown.Menu>
                                    </Dropdown>
                                </Table.Cell>
                            </Table.Row>
                        ))}
                    </Table.Body>
                </Table>
                : <></>}
        </div>
    )
}
